import React from 'react';
import BlockElement from './BlockElement/BlockElement';
import './Field.css'


export default function Posts(props){
    
    let fieldElements = props.blocks.map( block => <BlockElement key={block.id} id={block.id} class={block.class} src={block.src} empty={block.empty} type={block.type}/> )
    
    return(
        <div className='main'>
            <div className="area">
                { fieldElements }
            </div>

            <div>
                <div className='wrapper'>
                    <p className='box'>: {props.wheatCount}</p>
                    <button className='box' onClick={() => props.wheatCount > 0 ? props.sell(1) : alert(' ')}></button>
                </div>
                <div className='wrapper'>
                    <p className='box'>: {props.eggCount}</p>
                    <button className='box' onClick={() => props.eggCount > 0 ? props.sell(2) : alert(' ')}></button>
                </div>
                <div className='wrapper'>
                    <p className='box'>: {props.milkCount}</p>
                    <button className='box' onClick={() => props.milkCount > 0 ? props.sell(3) : alert(' ')}></button>
                </div>
                <div className='wrapper'>
                    <p className='box'>: {props.money} </p>
                </div>
            </div>
        </div>
    );
}