import React from 'react';
import Field from './Field';
import { connect } from 'react-redux';
import { sellActionCreator } from '../../redux/field-reducer';


let mapStateToProps = (state) => {
  return{
    blocks: state.fieldPage.blocks,
    money: state.fieldPage.money,
    wheatCount: state.fieldPage.wheatCount,
    eggCount: state.fieldPage.eggCount,
    milkCount: state.fieldPage.milkCount,
  }
}

let mapDispatchToProps = (dispatch) => {
  return{
    sell: (productId) => {
      dispatch(sellActionCreator(productId))
    }
  }
} 

const FieldContainer = connect(mapStateToProps, mapDispatchToProps)(Field);

export default FieldContainer;