import React from 'react';
import '../Field.css';
import Button from '@material-ui/core/Button';
import { openModule, collectItem } from '../../Menu/Menu';
// import { openModule } from '../../Menu/ActionMenu/ActionMenu';
// import { collectItem } from '../../Menu/CollectMenu/CollectMenu';

export default function BlockElement(props){
    
    return(
      <div>
        <div className={props.class}>
            <Button className='btn'>
              <img id={props.id} src={props.src} onClick={(e) => props.empty ? openModule(e) : collectItem(e)}/>
            </Button>
        </div>

      </div>
    );
}