import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import './Menu.css';

const useStyles = makeStyles({
  root: {
    minWidth: 275,
  },
  title: {
    fontSize: 14,
  },
});

let state = {
    id: '',
    type: ''
}

export const openModule = (e) => {
    state.id = e.target.id;
    document.querySelector('.actionMenu').style.display = 'block';  
}

export const collectItem = (e) => {
    state.id = e.target.id;
    if(state.id > 100 && state.id < 165){
        document.querySelector('.collectMenu').style.display = 'block';
    }else if(state.id > 200 && state.id < 265){
        document.querySelector('.feedMenu').style.display = 'block';
    }else if(state.id > 300 && state.id < 365){
        document.querySelector('.collectMenu').style.display = 'block';
    }else if(state.id > 400 && state.id < 465){
        document.querySelector('.feedMenu').style.display = 'block';
    }else if(state.id > 500 && state.id < 565){
        document.querySelector('.collectMenu').style.display = 'block';
    }
}

const closeModule = () => {
    document.querySelector('.actionMenu').style.display = 'none'; 
    document.querySelector('.collectMenu').style.display = 'none'; 
    document.querySelector('.feedMenu').style.display = 'none'; 
}

export default function Menu(props) {
    const classes = useStyles();

    const buyAction = (e) => {
        let type = e.target.id; 
        if(type == 1){
            props.setSeeds(state.id);
            let currentId = state.id;
            closeModule();
            setTimeout(() => props.buyItem(currentId, type), 5000);
        }else if(type == 2){
            closeModule();
            props.buyItem(state.id, type);
        }else if(type == 3){
            closeModule();
            props.buyItem(state.id, type);
        }

    }

    const collectAction = (e) => {        
        if(state.id > 100 && state.id < 165){
            closeModule();
            props.collectItem(state.id);
        }else if(state.id > 200 && state.id < 265){
            closeModule();
            setTimeout(() => props.feed(state.id), 5000);
        }else if(state.id > 300 && state.id < 365){
            props.collectItem(state.id)
            closeModule();
        }else if(state.id > 400 && state.id < 465){
            closeModule();
            setTimeout(() => props.feed(state.id), 5000);
        }else if(state.id > 500 && state.id < 565){
            props.collectItem(state.id)
            closeModule();
        }
    }

    const deleteItem = () => {
        props.delete(state.id);
        closeModule();
    }


    return (
        <div>
            <div className="mainActionMenu">
                <div className="actionMenu">
                    <Card className={classes.root}>
                    <CardContent>
                        <Typography className={classes.title} color="textSecondary" gutterBottom>
                             :
                        </Typography>
                        <button id='1' onClick={(e) => props.money >= 10 ? buyAction(e) : alert(' ')}> - 10 </button>
                        <button id='2' onClick={(e) => props.money >= 25 ? buyAction(e) : alert(' ')}> - 25 </button>
                        <button id='3' onClick={(e) => props.money >= 40 ? buyAction(e) : alert(' ')}> - 40 </button>
                    </CardContent>
                    <CardActions>
                        <Button size="small" onClick={closeModule}></Button>
                    </CardActions>
                    </Card>
                </div>
            </div>

            <div className="mainCollectMenu">
                <div className="collectMenu">
                    <Card className={classes.root}>
                    <CardContent>
                        <Typography className={classes.title} color="textSecondary" gutterBottom>
                             :
                        </Typography>
                        <Button onClick={(e) => collectAction(e)}></Button>
                    </CardContent>
                    <CardActions>
                        <Button size="small" onClick={closeModule}></Button>
                    </CardActions>
                    </Card>
                </div>
            </div>

            <div className="mainFeedMenu">
                <div className="feedMenu">
                    <Card className={classes.root}>
                    <CardContent>
                        <Typography className={classes.title} color="textSecondary" gutterBottom>
                             :
                        </Typography>
                        <Typography className={classes.title} gutterBottom>
                              
                        </Typography>
                        <Button onClick={(e) => props.wheatCount > 0 ? collectAction(e) : alert('  ')}></Button>
                        <Button onClick={(e) => deleteItem()}></Button>
                    </CardContent>
                    <CardActions>
                        <Button size="small" onClick={closeModule}></Button>
                    </CardActions>
                    </Card>
                </div>
            </div>

        </div>
    );
}
