import React from 'react';
import Menu from './Menu';
import { connect } from 'react-redux';
import { buyItemActionCreator, collectItemActionCreator, setSeedsActionCreator, feedActionCreator, deleteActionCreator } from '../../redux/field-reducer';


let mapStateToProps = (state) => {
  return{
    type: state.fieldPage.blocks,
    wheatCount: state.fieldPage.wheatCount,
    money: state.fieldPage.money,
  }
}

let mapDispatchToProps = (dispatch) => {
  return{
    buyItem: (id, type) => {
      dispatch(buyItemActionCreator(id, type));
    },
    collectItem: (id) => {
        dispatch(collectItemActionCreator(id));
    },
    setSeeds: (id) => {
        dispatch(setSeedsActionCreator(id));
    },
    feed: (id) => {
      dispatch(feedActionCreator(id))
    },
    delete: (id) => {
      dispatch(deleteActionCreator(id))
    }
  }
} 

const AreaContainer = connect(mapStateToProps, mapDispatchToProps)(Menu);

export default AreaContainer;